.breathing {
    color: white;
    border-radius: 12px;
    padding: 40px 10px;
    font-weight: 600;
    background: rgba(255, 255, 255, 0.5);
}

.main {
    display: flex;
    flex-wrap: wrap;
}

.description {
    flex: 1;
    padding: 20px;
}

.screens {
    flex: 1;
    text-align: end;
}

.logos {
    display: flex;
    margin: 20px 10px;
    justify-content: space-between;
}

img {
    width: 300px;
    height: auto;
}

.appstore a {
    margin: 10px;
    display: block;
    width: 200px;
    height: 80px;
    background-repeat: no-repeat;
    background-image: url(../assets/images/appstore.png);
}

.playstore a {
    margin-top: 20px;
    display: block;
    width: 200px;
    height: 80px;
    background-repeat: no-repeat;
    background-image: url(../assets/images/playstore.png);
}

.privacy {
    margin: 10px;
}